<template>
  <div>
    <div>
      <MusicListView title="最新歌单" :recommend="recommend" />
      <MusicRecommend title="推荐歌曲" :recommendmusic="recommendmusic" />
      
    </div>
  </div>
</template>

<script>
import MusicListView from "./MusicListView";
import MusicRecommend from './MusicRecommend'
export default {
  name: "MusicList",
  data() {
    return {
      recommend: [],
      recommendmusic:[]
    };
  },
  mounted() {
    // 最新歌单
    this.$api.getRecommend().then((res) => {
      this.recommend = res.playlists;
      console.log(res);
    }),
  // 推荐歌曲
    this.$api.getRecommendmusic().then((res) => {
        this.recommendmusic = res.result;
        // console.log(res)
          
      });
  },
  components: {
    MusicListView,
    MusicRecommend
    
  },
};
</script>

