import {get} from '../utils/request'

const api={
    // 获取最新歌单
    getRecommend(){
        return get("/top/playlist?order=new &limit=6")
    },
    // 登录接口
    login(params){
        return get("/login/cellphone",params)
    },
    // 推荐歌曲
    getRecommendmusic(){
        return get("/personalized/newsong")
    },
    //歌单详情
    getMusicDetails(params){
        return get("/playlist/detail",params)
      
    },
    // 播放接口
    getPlayer(params){
        return get("/song/url",params)
    },
    //搜索接口
    getSearch(params){
        return get("/search",params)
    }
}
export default api